.table {
  table-layout: fixed;

  tr {
    background-color: #ffffff;
    &:nth-child(odd) {
      background-color: #f9fafd; } }

  td {
    padding: 0 10px;
    display: inline-block;
    height: 30px;
    line-height: 30px;
    font-size: 14px;
    font-weight: 400;

    &:first-child {
      width: 79%;
      @include m(768) {
        width: 100%; } }

    &:last-child {
      text-align: right;
      width: 20%;
      @include m(768) {
        display: none; } }
    a {
      display: block;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap; } } }






