.container {
  position: relative;
  width: 1110px;
  margin: 0 auto;

  @include m($laptop) {
    width: 980px; }

  @include m($tablet) {
    width: 760px;
    padding: 0 20px; }

  @include m($mobile) {
    width: 100%; } }

.main {
  flex: 1 1 auto; }

.footer,
.foot {
  padding: 24px 0;
  background-color: $bg-grey;

  &__wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    @include m($tablet) {
      flex-direction: column; } }

  &__right {
    display: flex;
    flex-direction: row;
    align-items: center;
    @include m($mobile) {
      flex-direction: column;
      .nav {
        margin-bottom: 10px; } } } }
